"use client"
import React from "react";
import { useCart } from "../../components/CartContext";

const CartPage: React.FC = () => {
  const { cart, removeFromCart, clearCart } = useCart();

  return (
    <div className="max-w-4xl mx-auto p-6">
      <h1 className="text-2xl font-bold">Your Cart</h1>
      {cart.length === 0 ? (
        <p>Your cart is empty.</p>
      ) : (
        <div>
          {cart.map((item: { productId: React.Key | null | undefined; name: string | number | bigint | boolean | React.ReactElement<unknown, string | React.JSXElementConstructor<any>> | Iterable<React.ReactNode> | React.ReactPortal | Promise<string | number | bigint | boolean | React.ReactPortal | React.ReactElement<unknown, string | React.JSXElementConstructor<any>> | Iterable<React.ReactNode> | null | undefined> | null | undefined; price: string | number | bigint | boolean | React.ReactElement<unknown, string | React.JSXElementConstructor<any>> | Iterable<React.ReactNode> | React.ReactPortal | Promise<string | number | bigint | boolean | React.ReactPortal | React.ReactElement<unknown, string | React.JSXElementConstructor<any>> | Iterable<React.ReactNode> | null | undefined> | null | undefined; }) => (
            <div key={item.productId} className="flex justify-between items-center p-4 border">
              <p>{item.name}</p>
              <p>${item.price}</p>
              <button onClick={() => removeFromCart(item.productId)} className="text-red-500">
                Remove
              </button>
            </div>
          ))}
          <button onClick={clearCart} className="mt-4 px-4 py-2 bg-red-500 text-white rounded-lg">
            Clear Cart
          </button>
        </div>
      )}
    </div>
  );
};

export default CartPage;
